@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* fonts */

body{
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
    transition: 0.2s ease;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -moz-user-select: none;
}

.App {
    background-color: #ecf0f1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height:100vh;
}

a{
    text-decoration: none;
}

.bg-dark, .dropdown-menu.show {
    background-color: #2c3e50 !important;
}

.dropdown-menu{
    min-width: 12.5rem !important;
}
.nav-link{
    padding: 3px 10px !important;
    color: #fff !important;
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
}
.nav-link:hover{
    color: #ff7f47 !important;
    border-top: 1px solid #ff7f47;
    border-bottom: 1px solid #ff7f47;
    transition: 0.5s;
}
.user-img{
    width: 70px;
    height: 70px;
    line-height: 70px;
    border-radius:50%;
    background-color: #354352;
    color: #fff;
    margin: 20px auto;
    text-align: center;
}

@media only screen and (min-width: 1400px) and (max-width:1450px) {
    .dropdown-menu{
        min-width: 11rem !important;
    }
}
@media only screen and (min-width: 1040px) and (max-width:1270px) {
    .dropdown-menu{
        min-width: 10.9rem !important;
    }
}
@media only screen and (min-width: 992px) and (max-width:1270px) {
    .dropdown-menu{
        min-width: 9.9rem !important;
        font-size: 13px !important;
    }
}
@media only screen and (min-width: 320px) and (max-width:992px) {
    .header-container{
        text-align: center;
    }
}


.banner img{
    width: 100%;
}
.banner h1{
    font-size:80px;
    line-height: 90px;
}
.banner h3{
    font-size:30px;
    color: #ff7f47;
}
.carousel-caption{
    width: 100% !important;
    top: 50% !important;
    left: 50% !important;
    -webkit-transform: translate(-50%, -50%) !important;
            transform: translate(-50%, -50%) !important;
}
.carousel-indicators{
    top: 90% !important;
}

@media only screen and (min-width: 900px) and (max-width:1040px) {
    .banner h1{
        font-size:60px;
        line-height: 70px;
    }
    .banner h3{
        font-size:27px;
    }
}
@media only screen and (min-width: 767px) and (max-width:900px) {
    .banner h1{
        font-size:40px;
        line-height: 50px;
    }
    .banner h3{
        font-size:25px;
    }
}
@media only screen and (min-width: 675px) and (max-width:767px) {
    .banner h1{
        font-size:35px;
        line-height: 45px;
    }
    .banner h3{
        font-size:23px;
    }
}
@media only screen and (min-width: 450px) and (max-width:675px) {
    .banner h1{
        font-size:25px;
        line-height: 35px;
    }
    .banner h3{
        font-size:15px;
    }
}
@media only screen and (min-width: 320px) and (max-width:450px) {
    .banner h1{
        font-size:20px;
        line-height: 28px;
    }
    .banner h3{
        font-size:12px;
    }
    .carousel-caption{
        width: 100% !important;
        top: 40% !important;
        left: 50% !important;
        -webkit-transform: translate(-50%, -50%) !important;
                transform: translate(-50%, -50%) !important;
    }
}

@media only screen and (min-width: 280px) and (max-width:767px) {
    .banner button{
        width:50% !important;
    }
}

.news-container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}
@media only screen and (min-width: 767px) and (max-width:992px) {
    .news-container{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}
@media only screen and (min-width: 280px) and (max-width:767px) {
    .news-container{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
}

.newsletter{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.newsletter-img{
    text-align: right;
}
.newsletter img{
    width: 80%;
}
@media only screen and (min-width: 320px) and (max-width:1025px) {
    .newsletter{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
    .newsletter-img{
        text-align: center;
    }
}
.img-scale-main .card{
    position: relative;
    overflow: hidden;
}
.img-scale-main .card:hover{
	box-shadow: 0 5px 14px rgba(0, 0, 0, .25);
}
.img-scale-main .card{
    width: 100%;
    height: 100%;
}
.img-scale-main .card:hover .img-scale img{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 1s;
}
.img-scale-main .card-body{
    width: 100%;
    z-index: 5;
    background-color:#fff;
    padding-bottom: 0px !important;
}
.popular-tour-container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

@media only screen and (min-width: 767px) and (max-width:992px) {
    .popular-tour-container{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}

@media only screen and (min-width: 280px) and (max-width:767px) {
    .popular-tour-container{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
    .popular-tour button{
        width:75% !important;
    }
}


.tours-container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}
@media only screen and (min-width: 767px) and (max-width:992px) {
    .tours-container{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}
@media only screen and (min-width: 280px) and (max-width:767px) {
    .tours-container{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
    .tour button{
        width:75% !important;
    }
}

.confirm-order input, .confirm-order select, .confirm-order textarea{
    border: 1px solid #e1e1e1;
    box-sizing: border-box;
    border-radius: 6px;
    color: #ff7f47;
    padding:8px 10px;
    font-size:14px;
    outline: none;
}
.confirm-order input:active, .confirm-order select:active, .confirm-order textarea:active{
    border: 1px solid #ff7f47;
}
.confirm-order input:hover, .confirm-order select:hover, .confirm-order textarea:hover{
    border: 1px solid #ff7f47;
    transition: 1s;
}
.confirm-order ::-webkit-input-placeholder {
    color: #a5a5a5 ;
  }
.confirm-order :-ms-input-placeholder {
    color: #a5a5a5 ;
  }
.confirm-order ::placeholder {
    color: #a5a5a5 ;
  }

.swal2-icon.swal2-success .swal2-success-ring {
    border: 0.25em solid #ff7f47 !important;
}
.swal2-icon.swal2-success [class^=swal2-success-line] {
    background-color: #ff7f47 !important;
}

  @media only screen and (min-width: 280px) and (max-width:992px) {
    .order-extra{
        padding-bottom: 35px;
      }
    .confirm-order {
        border-top: 1px solid #a5a5a5;
    }
    .confirm-order form{
        margin-top: 35px;
    }
    .confirm-order form input, .confirm-order form select, .confirm-order form textarea{
        width: 100% !important;
    }
}
.tour-details{
    width: 100%;
    margin: 0 auto;
    padding: 20px;
    display: grid;
    grid-template-columns:  repeat(2, 1fr);
    box-shadow: rgb(0 0 0 / 20%) 0px 2px 1px -1px,
        rgb(0 0 0 / 14%) 0px 1px 1px 0px,
        rgb(0 0 0 / 12%) 0px 1px 3px 0px;
}
.tour-details p{
    margin-bottom: 0px;
}
.confirm-order form{
    display: flex;
    flex-direction: column;
}
.confirm-order form input, .confirm-order form textarea, .confirm-order form select{
    width: 75%;
    margin-bottom: 10px;
}
@media only screen and (min-width: 280px) and (max-width:992px) {
    .tour-details{
        display: grid !important;
        grid-template-columns: repeat(1, 1fr);
    }
}
.contact-us{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    background-color: #ff7f47;
    border-radius: 5px;
}

@media only screen and (min-width: 280px) and (max-width:492px) {
    .contact-us{
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
}

@media only screen and (min-width: 280px) and (max-width:767px) {
    .contact button{
        width:75% !important;
    }
}

h3{
    color: #ff7f47
}

@media only screen and (min-width: 280px) and (max-width:1024px) {
    .login button{
        width:90% !important;
    }
}

